import React, { Component } from 'react'
import Header from "../../../textbook/components/header"
import "./style.scss"

const ShowBox = (props)=>(
    <div className="mui-card showBox" style={{padding:"10px 5px",background:props.right?"#04f52c":"#f70303"}} >
    {props.right?(
        <>
            <h3>✔ 恭喜您，回答正确！ </h3>
        </>
    ):(
        <>
            <h3>❌ 回答错误！</h3>
            <h4>解析：{props.question.explains}</h4>
         </>
    )}
</div>
)

const Detail =(props)=>(
    <div className="mui-card" style={{margin:0}}>
    <img src={props.question.url||"http://img1.imgtn.bdimg.com/it/u=2836943063,515072414&fm=26&gp=0.jpg"} alt="图片"/>
     <h3 id="question"
     style={{padding:"20px 10px"}}>{props.question.question}</h3>
        {props.secletion.map((item,index)=>(
          <div key={item} 
          className="item">
             <label>{item}</label>
             <input
             onChange={props.changeAction.bind(this,index,props.question.answer)} 
             name="radio" type="radio" />
           </div>
        ))
    }
</div>
)

const getItem = (currItem)=>{
    let itemArr = [];
        for(let key in currItem){
            if(key.startsWith("item") && currItem[key]){
                itemArr.push(currItem[key]);
            }
        }
    return itemArr;
}

export default class questionDetail extends Component {
    constructor(props){
        super(props);
        let id = props.location.state.id;
        let data = props.location.state.question;
        let currItem =data.filter(item=>{
            if(id===item.id){
                return item;
            }
        })[0];
        
        this.state = {
            question:currItem,
            allQuest:data,
            currId:id,
            currIndex:0,
            secletion:getItem(currItem),
            isShow:false,
            right:false
        }
    }

    render() {
        let {question,secletion,isShow,right} = this.state;
        return (
        <div className="page" 
        id="questionDetail"
        style={{ zIndex: 1000,bottom:0}}>
            <Header centent="题目详情" 
            back={this.back.bind(this)}/>
            <Detail 
            secletion={secletion}
            question={question}
            changeAction={this.changeAction.bind(this)}
            />

            {   
                isShow&&(<ShowBox
                question={question}
                right={right}
                />)
            }
            <p className="next" onClick={this.nextAction.bind(this)}>下一题</p>
		</div>
        )
    }

    changeAction(index,answer){
        let rs = parseInt(answer);
        let flag = (index+1)===rs?true:false;
        this.setState({
            right:flag,
            isShow:true
        })
    }

    back(){
        this.props.history.goBack();
    }
    
    nextAction(){
        let {allQuest,currId,currIndex} = this.state;
         allQuest.forEach((item,index) => {
                if(item.id===currId){
                   let arr = getItem(this.state.allQuest[currIndex]);
                    this.setState({
                        currIndex:index+1,
                        currId:this.state.allQuest[index+1].id,
                        question: this.state.allQuest[this.state.currIndex],
                        secletion:arr,
                        isShow:false,
                        right:false
                 });
                }
         })
    }
}
