import React from 'react'
import "./style.scss"

const rightAction = ()=>{};
export default (props)=>(
    <header className="border-bottom" id="onlyHead">
        <span 
        className="iconfont icon-fanhui"
        onClick={props.back}></span>
        <div className="centent">
            {props.centent}
        </div>
        <div className="right" 
        onClick={props.rightAction?props.rightAction:rightAction}>
            {props.right}
        </div>
    </header>
)
